.wp-block-create-block-text-box {
	background-color: #000000;
	color: #fff;
	padding: 2px;
	&.text-box-align-right {
		text-align: right;
	}

	&.text-box-align-center {
		text-align: center;
	}

	&.text-box-align-left {
		text-align: left;
	}
}
